.backdrop {
  width: 100vw;
  height: 100%;
  z-index: 10;
  opacity: 1;
  visibility: visible;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s linear, visibility .25s linear;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

